import React, { useContext } from 'react'
import { GrCart } from 'react-icons/gr'
import CartContext from '../store/cart-context';

export default function Header({setShowCartItem}) {

    const cartCtx = useContext(CartContext);
    const noOfCartItems = cartCtx.items.length >0 ? cartCtx.items.reduce((number, item) => number + +item.amount,0) : 0;

    return (
        <>
            <nav class="navbar fixed-top navbar-light bg-light">
                <div class="container-fluid">
                    <a class="navbar-brand" href='#'><b>Food Order</b></a>
                    <form class="d-flex">
                        {/* <input class="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button class="btn btn-outline-success" type="submit">Search</button> */}
                        <div className="cart-btn" onClick={()=>setShowCartItem(true)}>
                            <a class="btn btn-info btn-lg" href='#'>
                                <GrCart style={{marginTop:-4}}/> Cart &nbsp;
                                <span className="badge rounded-pill bg-primary">{noOfCartItems}</span>
                            </a>
                        </div>
                    </form>
                </div>
            </nav>
            <br/><br/>
        </>
    )
}
