import React, { useReducer } from 'react';
import { toast } from 'react-toastify';
import CartContext from './cart-context'


const defaultCartState = {
    items: [],
    totalAmount: 0
}

const cartReducer = (state, action) => {
    if (action.type === 'ADD_ITEM') {
        const existingIndex = state.items.findIndex(item => item.id === action.item.id);
        let updatedItems;
        if (existingIndex > -1) {
            const existingCartItem = state.items[existingIndex];
            const updatedItem = { ...existingCartItem, amount: existingCartItem.amount + +action.item.amount };
            updatedItems = [...state.items];
            updatedItems[existingIndex] = updatedItem;

        } else {
            updatedItems = state.items.concat(action.item);
        }

        const updateTotalAmount = (state.items.length > 0 ? state.totalAmount : 0) + action.item.price * action.item.amount;
        toast.success("(" + action.item.amount + ") " + action.item.title + " Succesfully added in cart");
        return {
            items: updatedItems,
            totalAmount: updateTotalAmount
        }
    } else if (action.type === 'REMOVE_ITEM') {
        const existingIndex = state.items.findIndex(item => item.id === action.id);
        const existingCartItem = state.items[existingIndex];
        const updateTotalAmount = state.totalAmount - existingCartItem.price;
        let updatedItems;
        if (existingCartItem.amount === 1) {
            updatedItems = state.items.filter(e => e.id !== action.id);
        } else {
            const updatedItem = { ...existingCartItem, amount: existingCartItem.amount - 1 };
            updatedItems = [...state.items];
            updatedItems[existingIndex] = updatedItem;
        }
        return {
            items: updatedItems,
            totalAmount: updateTotalAmount
        }
    } else if(action.type === 'RESET_ITEM'){
        return {
            items: [],
            totalAmount: 0
        }
    }
}

const CartProvider = (props) => {

    const [cartState, dispatchCartAction] = useReducer(cartReducer, defaultCartState);

    const addItemToCart = (item) => {
        dispatchCartAction({ type: 'ADD_ITEM', item: item })
    }
    const removeItemToCart = (id) => {
        dispatchCartAction({ type: 'REMOVE_ITEM', id: id })
    }
    const resetCart = () => {
        dispatchCartAction({ type: 'RESET_ITEM'})
    }
    const cartContext = {
        items: cartState.items,
        totalAmount: cartState.totalAmount,
        addItem: addItemToCart,
        removeIem: removeItemToCart,
        resetCart
    }
    return <CartContext.Provider value={cartContext}>
        {props.children}
    </CartContext.Provider>
}

export default CartProvider;