import React from 'react';
import ReactDOM from 'react-dom';

import './modal.css';

const Backdrop = props => {
    return <div className='backdrop' onClick={()=>props.setShowCartItem(false)}/>
}

const ModalOverlay = props => {
    return <div className='custom_modal'>
        <div className='custom_modal_content'>{props.children}</div>
    </div>
}

const portalElement = document.getElementById("overlays");
export default function Modal(props) {
    return (
        <>
          {ReactDOM.createPortal(<Backdrop setShowCartItem={props.setShowCartItem}/>,portalElement)}
          {ReactDOM.createPortal(<ModalOverlay>{props.children}</ModalOverlay>,portalElement)}
        </>
    )
}
