import React, { useContext, useReducer, useState } from 'react'
import CartContext from '../store/cart-context';
import CartItem from './CartItem';
import Modal from '../UI/Modal';
import './cart.css'
import CheckoutForm from '../Checkout/CheckoutForm';
import { toast } from 'react-toastify';

export default function Cart({ setShowCartItem }) {

    const [orderConfirm, setOrderConfirm] = useState(false);
    const cartCtx = useContext(CartContext);

    const totalAmount = cartCtx.totalAmount;

    const cartItemAddHadler = (item) => {
        cartCtx.addItem({ ...item, amount: 1 });
    }
    const cartItemRemoveHandler = (id) => {
        cartCtx.removeIem(id);
    }

    const order = () => {
        if (cartCtx.items.length)
            setOrderConfirm(true);
        else
            toast.warning("item not available in Cart for Order")
    }
    
    return (
        <Modal setShowCartItem={setShowCartItem}>
            <div className='cart_meal_list'>
                <h3>Cart Items:</h3><hr /><br />
                <div className='meal_container'>
                    <ul>
                        {cartCtx.items.map(e => <CartItem {...e} key={e.id} onRemove={cartItemRemoveHandler.bind(null, e.id)} onAdd={cartItemAddHadler.bind(null, e)} />)}
                    </ul>
                    {cartCtx.items.length === 0 && <h3>No item available in Cart</h3>}
                </div>
                {orderConfirm && <CheckoutForm setShowCartItem={setShowCartItem} />}
                <div className="meal_modal_footer">
                    <h2>Total Amount: &#8377;{totalAmount}</h2>
                    {!orderConfirm && <> <br />
                        <button onClick={() => setShowCartItem(false)}>Close</button>
                        <button onClick={order}>Order</button></>
                    }
                </div>
            </div>
        </Modal>
    )
}
