import React, { useContext, useRef, useState } from 'react'
import { toast } from 'react-toastify';
import CartContext from '../store/cart-context';
import Input from '../UI/Input'
import './checkout.css'

export default function CheckoutForm(props) {

    const cartCtx = useContext(CartContext)

    const nameRef = useRef();
    const addressRef = useRef();
    const pincodeRef = useRef();
    const mobilenoRef = useRef();


    const submitHandler = () => {
        const name = nameRef.current.value;
        const address = addressRef.current.value;
        const pincode = pincodeRef.current.value;
        const mobileno = mobilenoRef.current.value;

        if (name.length < 3) {
            toast.warn("Please enter valid name");
            return;
        }
        if (address.length < 8) {
            toast.warn("Please enter valid address");
            return;
        }
        if (pincode.length !== 6) {
            toast.warn("Please enter valid pin-code");
            return;
        }
        if (mobileno.length !== 10) {
            toast.warn("Please enter valid mobile no.");
            return;
        }

        toast.success(name+ " Your Order is successfully submit");
        props.setShowCartItem(false);
        cartCtx.resetCart();
    }

    return (
        <>
            <h4>Order Confirmation</h4>
            <form className='checkout_form' onSubmit={submitHandler}>
                <Input ref={nameRef} id="name" label="Name &nbsp; &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" />
                <Input ref={addressRef} id="address" label="Address &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" />
                <Input ref={pincodeRef} id="pincode" label="Pin Code &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" />
                <Input ref={mobilenoRef} id="mobileno" input={{ type: 'number', minlength: 10, maxlength: 10, style: { width: 256 } }} label="Mob No.&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" />
                <div className="meal_modal_footer">
                    <button onClick={() => props.setShowCartItem(false)}>Close</button>
                    <button>Conrifm</button>
                </div>
            </form>
        </>
    )
}
