import React, { useState, useRef } from 'react'
import Input from '../UI/Input'

export default function MealItemForm(props) {
    const [amountIsValid, setAmountIsValid] = useState(true);
    const inputRef = useRef(null);

    const submitHandler = (e) => {
        e.preventDefault();

        const enteredAmount = inputRef.current.value;

        if(enteredAmount.trim().length===0|| enteredAmount > 5 || enteredAmount < 1)
        setAmountIsValid(false);

        props.onAddToCart(enteredAmount);
    }

    return (
        <form onSubmit={submitHandler}>
            <Input ref={inputRef} id="amount" label="Amount" input={{ type: 'number', min: 1, max: 5 }} className="amount_input" />
            <button className='amount_add_btn'>Add</button>
            {!amountIsValid && <p>Enter a valid amount (1-5)</p>}
        </form>
    )
}
